import React, { useState, useEffect, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  SET_STATUS_JAWAB_SOAL,
  SET_JAWABAN_SOAL_PG,
  SET_HASIL_JAWAB_SOAL,
  SET_JAWABAN_SOAL_MENJODOHKAN,
} from "../../../redux/actions";
import { CInputRadio, CLabel, CFormGroup, CCol } from "@coreui/react";
import { request, endpoints, generateHTML } from "../../../utils";
import { Line } from "react-lineto";

function MenjodohkanVertikal(props) {
  const dispatch = useDispatch();
  const dataSoal = useSelector((state) => state.dataSoal);
  const userSoal = useSelector((state) => state.userSoal);
  const DATA_SOAL = dataSoal.soal[dataSoal.current.index];
  // const { dataSoal: DATA_SOAL } = dataSoal;
  const prevScrollY = useRef(0);
  const [goingUp, setGoingUp] = useState(false);

  // ----------
  const { exam_question_answer: data_jawaban } = DATA_SOAL;
  const [dataJawaban, setDataJawaban] = useState({});
  const leftRef = useRef([]);
  const rightRef = useRef([]);
  const [lines, setLines] = useState([]);
  const [lastItemClicked, setLastItemClicked] = useState({});
  const [containerDimension, setContainerDimension] = useState({
    height: 0,
    width: 0,
  });
  const [dimensions, setDimensions] = React.useState({
    height: window.innerHeight,
    width: window.innerWidth,
  });
  const [left, right] = data_jawaban.pola_jawaban;

  // ------------- METHODS ------------------ //
  const lineExists = (l, r) => {
    let isExist = -1;
    const sj = data_jawaban.pola_jawaban[2].status_jawaban;
    for (let i = 0; i < sj.length; i++) {
      const [il, ir] = sj[i];
      if (il == l && ir == r) {
        isExist = i;
        break;
      }
    }
    return isExist;
  };
  const onListItemClicked = (item) => {
    if (Object.keys(lastItemClicked).length === 0) {
      return setLastItemClicked({ ...item });
    }
    if (item.label === lastItemClicked.label) {
      return setLastItemClicked({ ...item });
    }

    const sj = data_jawaban.pola_jawaban[2].status_jawaban;

    // set status jawaban soal
    dispatch({
      type: SET_STATUS_JAWAB_SOAL,
      payload: { status: "dijawab" },
    });

    if (item.label === dataJawaban.right.label) {
      let exist = lineExists(
        lastItemClicked.status_jawaban,
        item.status_jawaban
      );
      if (exist >= 0) {
        const newSJ = sj.filter((item, index) => {
          if (index === exist) {
            return false;
          }
          return true;
        });
        // console.log("---->newSJ", newSJ);
        if (newSJ.length === 0) {
          setLines([]);
        }

        dispatch({
          type: SET_JAWABAN_SOAL_MENJODOHKAN,
          payload: newSJ,
        });
      } else {
        // add lines
        sj.push([
          String(lastItemClicked.status_jawaban),
          String(item.status_jawaban),
        ]);

        dispatch({ type: SET_JAWABAN_SOAL_MENJODOHKAN, payload: sj });
      }
    } else {
      let exist = lineExists(
        item.status_jawaban,
        lastItemClicked.status_jawaban
      );
      if (exist >= 0) {
        const newSJ = sj.filter((item, index) => {
          if (index === exist) {
            return false;
          }
          return true;
        });
        // console.log("---->newSJ", newSJ);
        if (newSJ.length === 0) {
          setLines([]);
        }
        dispatch({
          type: SET_JAWABAN_SOAL_MENJODOHKAN,
          payload: newSJ,
        });
      } else {
        sj.push([
          String(item.status_jawaban),
          String(lastItemClicked.status_jawaban),
        ]);
        dispatch({ type: SET_JAWABAN_SOAL_MENJODOHKAN, payload: sj });
      }
    }
    setLastItemClicked({});
  };
  useEffect(() => {
    function handleResize() {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth,
      });
    }

    // function containerResize() {
    //   setContainerDimension({
    //     width: container.getBoundingClientRect().width,
    //     height: container.getBoundingClientRect().height,
    //   });
    // }
    // const container = document.getElementById("menjodohkan-container");
    // const resizeObserver = new ResizeObserver((el) => containerResize());
    // resizeObserver.observe(container);
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
      // container.removeEventListener("resize", handleResize);
    };
  });

  // useEffect(() => {
  //   const handleScroll = () => {
  //     const currentScrollY = window.scrollY;
  //     if (prevScrollY.current < currentScrollY && goingUp) {
  //       setGoingUp(false);
  //     }
  //     if (prevScrollY.current > currentScrollY && !goingUp) {
  //       setGoingUp(true);
  //     }

  //     prevScrollY.current = currentScrollY;
  //     console.log(goingUp, currentScrollY);
  //   };

  //   window.addEventListener("scroll", handleScroll, { passive: true });

  //   return () => window.removeEventListener("scroll", handleScroll);
  // }, [goingUp]);

  useEffect(() => {
    window.scrollTo(0, 0);
    function separate() {
      left.item = [];
      right.item = [];
      for (let i = 0; i < data_jawaban.jawaban.length; i++) {
        const item = data_jawaban.jawaban[i];
        if (item.label === left.label) {
          left.item.push(item);
        } else {
          right.item.push(item);
        }
      }
      leftRef.current = [];
      rightRef.current = [];
      setDataJawaban({ left, right });
    }
    separate();
    setTimeout(() => {
      setDimensions({ height: window.innerHeight, width: window.innerWidth });
    }, 1500);
  }, []);

  useEffect(() => {
    function reCreateLines() {
      const newLines = [];
      const statusjawaban = data_jawaban.pola_jawaban[2].status_jawaban;
      if (statusjawaban.length === 0) return;
      if (leftRef.current.length === 0 || rightRef.current.length === 0) return;
      for (let i = 0; i < statusjawaban.length; i++) {
        const [leftIndex, rightIndex] = statusjawaban[i];
        console.log(statusjawaban[i]);
        const p1 = leftRef.current[leftIndex - 1].getBoundingClientRect();
        const p2 = rightRef.current[rightIndex - 1].getBoundingClientRect();
        newLines.push({
          x0: p1.x + p1.width,
          y0: p1.y + p1.height / 2,
          x1: p2.x,
          y1: p2.y + p2.height / 2,
        });
        console.log(newLines);
      }
      // console.log("lines -->", newLines);
      setTimeout(() => {
        setLines(newLines);
      }, 0);
    }
    reCreateLines();
  }, [
    leftRef.current,
    rightRef.current,
    dimensions,
    dataJawaban,
    data_jawaban,
  ]);

  // ----------

  useEffect(() => {
    if (dataSoal.status[dataSoal.current.index].status === "dijawab") {
      const data = {
        exam_user_question_id: userSoal.id_soal[dataSoal.current.index],
        pola_jawaban:
          dataSoal.soal[dataSoal.current.index].exam_question_answer
            .pola_jawaban,
      };
      const config = {
        ...endpoints.jawabSoalPilgan,
        data,
      };
      request(null, config)
        .then((res) => {
          dispatch({
            type: SET_HASIL_JAWAB_SOAL,
            payload: { hasil: res.data.status_jawaban },
          });
        })
        .catch((err) => console.log(err));
    }
  }, [DATA_SOAL]);

  return (
    <div className="row">
      {lines.map((item, index) => {
        return (
          // <span>Kuda</span>
          <Line
            borderColor="red"
            borderWidth={5}
            key={index}
            x0={item.x0}
            x1={item.x1}
            y0={item.y0}
            y1={item.y1}
          />
        );
      })}
      <div className="col-12">
        {DATA_SOAL.exam_question_articles.map((item, index) => {
          return (
            <div className="mb-2" key={index}>
              <p className="text-center font-weight-bold">{item.judul}</p>
              <div
                dangerouslySetInnerHTML={{ __html: generateHTML(item.artikel) }}
              />
            </div>
          );
        })}
        <p
          className="text-primary mb-1"
          dangerouslySetInnerHTML={{
            __html: generateHTML(DATA_SOAL.exam_question_answer.pertanyaan),
          }}
        />
        {/* menjodohkan jawaban */}
        <CFormGroup row id="menjodohkan-container">
          <CCol xs={5}>
            <ul className="list-group">
              {Object.keys(dataJawaban).length &&
                dataJawaban.left.item.map((item, index) => {
                  return (
                    <li
                      style={{ cursor: "pointer" }}
                      onClick={() => {
                        onListItemClicked(item, index + 1);
                      }}
                      key={index}
                      ref={(el) => (leftRef.current[index] = el)}
                      className={`left-${index} text-center list-group-item list-group-item-action ${
                        lastItemClicked.text_jawaban === item.text_jawaban
                          ? "active"
                          : ""
                      }`}
                    >
                      {item.text_jawaban}
                    </li>
                  );
                })}
            </ul>
          </CCol>
          <CCol xs={2} />
          <CCol xs={5}>
            <ul className="list-group">
              {Object.keys(dataJawaban).length &&
                dataJawaban.right.item.map((item, index) => {
                  return (
                    <li
                      style={{ cursor: "pointer" }}
                      onClick={() => {
                        onListItemClicked(item, index + 1);
                      }}
                      key={index}
                      ref={(el) => (rightRef.current[index] = el)}
                      className={`right-${index} text-center list-group-item list-group-item-action ${
                        lastItemClicked.text_jawaban === item.text_jawaban
                          ? "active"
                          : ""
                      }`}
                    >
                      {item.text_jawaban}
                    </li>
                  );
                })}
            </ul>
          </CCol>
        </CFormGroup>
        {/* {DATA_SOAL.exam_question_answer.jawaban.map((item, index) => {
          return (
            <CFormGroup key={index} variant="custom-radio">
              <CInputRadio
                custom
                name="radio-input-jawaban"
                id={`radio-layout-soal-${index}`}
                checked={Boolean(parseInt(item.status_jawaban))}
                onChange={(e) => {
                  dispatch({ type: SET_JAWABAN_SOAL_PG, payload: index });
                  dispatch({
                    type: SET_STATUS_JAWAB_SOAL,
                    payload: { status: "dijawab" },
                  });
                }}
              />
              <CLabel
                variant="custom-checkbox"
                htmlFor={`radio-layout-soal-${index}`}
              >
                {mapNumToChar(index)}
                <span
                  className="pilgan-answer ml-3"
                  dangerouslySetInnerHTML={{
                    __html: generateHTML(item.text_jawaban, true),
                  }}
                />
              </CLabel>
            </CFormGroup>
          );
        })} */}
      </div>
    </div>
  );
}

export default MenjodohkanVertikal;
