import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import {
  CButton,
  CModal,
  CModalBody,
  CModalFooter,
  CModalTitle,
  CModalHeader,
  CInput,
  CRow,
  CCard,
  CFormGroup,
  CCol,
  CToast,
  CToastBody,
  CToastHeader,
  CToaster,
} from "@coreui/react";
import { useSelector, useDispatch } from "react-redux";
import { useHistory, Redirect } from "react-router-dom";
import Countdown from "react-countdown";
import {
  SET_CURRENT_SOAL,
  SET_DATA_SOAL,
  SET_STATUS_JAWAB_SOAL,
  SET_ID_USER_SOAL,
} from "../../../redux/actions";

// jenis soal
import {
  PilihanGanda,
  PilihanGandaVertical,
  MultipleSelection,
  MultipleSelectionVertical,
  BenarSalahVertikal,
  BenarSalahHorizontal,
  ShortEssay,
  SurveyLingkungan,
  Menjodohkan,
  MenjodohkanVertical,
} from "../../Soal";

import endpoints from "../../../utils/endpoints";
import request from "../../../utils/request";
import moment from "moment";
import Header from "../Header";
import PageLoading from "../../Loading/PageLoading";

function Exam() {
  const dispatch = useDispatch();
  const history = useHistory();
  const dataSoal = useSelector((state) => state.dataSoal);
  const dataExam = useSelector((state) => state.dataExam);
  const { profile } = useSelector((state) => state.user);
  const [alertMessage, setAlertMessage] = useState("");
  const { totalSoal: totalSoalUjian, waktu_server } = useSelector(
    (state) => state.userSoal
  );
  const [showModalNavigation, setShowModalNavigation] = useState(false);
  const [showModalInformasi, setShowModalInformasi] = useState(false);
  const [confirmExit, setConfirmExit] = useState(false);
  const [loading, setLoading] = useState(false);
  const [modal, setModal] = useState(false);
  const [timeLeft, setTimeLeft] = useState(5400000);
  const [
    showModalFinishConfirmation,
    setShowModalFinishConfirmation,
  ] = useState(false);

  const toggleModalNavigation = () => {
    setShowModalNavigation(!showModalNavigation);
  };
  const toggleModalFinishConfirmation = () => {
    setShowModalFinishConfirmation(!showModalFinishConfirmation);
  };
  const toggleShowModalInformasi = () => {
    setShowModalInformasi(!showModalInformasi);
  };
  const toggle = () => {
    setModal((state) => !state);
  };

  // const [countDown, setCountDown] = useState(
  //   new Date(waktu_server).getTime() +
  //     dataExam.current.lama_pengerjaan_soal * 60000
  // );

  // Toats -------------------------------------------------

  const [toasts, setToasts] = useState([]);
  const toasters = (() => {
    return toasts.reduce((toasters, toast) => {
      toasters[toast.position] = toasters[toast.position] || [];
      toasters[toast.position].push(toast);
      return toasters;
    }, {});
  })();

  // -----------------------------------------------------

  const addToast = ({ success, message }) => {
    setToasts([
      ...toasts,
      {
        position: "top-right",
        success,
        message,
        autohide: 4000,
        closeButton: true,
        fade: true,
      },
    ]);
  };

  // -----------------------------------------------------

  const strip_html_tags = (str) => {
    if (str === null || str === "") return false;
    else str = str.toString();
    return str.replace(/<[^>]*>/g, "");
  };
  // -----------------------------------------------------

  const generateHTML = (html, isOption = false) => {
    const includeImage = html.split("data:image");
    html = html.replace(
      '<p data-f-id="pbf" style="text-align: center; font-size: 14px; margin-top: 30px; opacity: 0.65; font-family: sans-serif;">Powered by <a href="https://www.froala.com/wysiwyg-editor?pb=1" title="Froala Editor">Froala Editor</a></p>',
      ""
    );
    if (!isOption) return html;
    if (includeImage.length > 1) {
      return html;
    } else {
      return strip_html_tags(html);
    }
  };

  // -----------------------------------------------------

  const confirmTimeEnd = async () => {
    const config = {
      ...endpoints.getExamTimeLeft,
      endpoint: endpoints.getExamTimeLeft.endpoint + dataExam.current.id,
    };
    const { status, data } = await request({}, config);
    if (data <= 0) {
      sendLog(data);
      quitExam();
    } else {
      setTimeLeft(data);
    }
  };

  // -----------------------------------------------------

  const sendLog = (timeLeftbackend) => {
    try {
      const config = {
        ...endpoints.createLog,
        data: {
          key: "waktu-habis",
          value: {
            timeLeftbackend,
            user: {
              nama: profile.nama,
              username: profile.username,
              sekolah: profile.school_nama,
            },
            ujian: {
              examUserId: dataExam.currentUser.id,
              id: dataExam.current.id,
              token: dataExam.current.token,
              waktu_mulai: new Date(dataExam.current.waktu_mulai) || null,
              waktu_selesai: new Date(dataExam.current.waktu_selesai) || null,
            },
            device: {
              time: new Date(),
              browser: {
                ua: window.navigator.userAgent || null,
                app: window.navigator.appVersion || null,
                connection: window.navigator.connection.effectiveType || null,
              },
            },
          },
        },
      };

      request(null, config)
        .then((res) => console.log("Log sent"))
        .catch((err) => console.log("Log not sent"));
    } catch (error) {
      console.log("Log not sent");
    }

    // alert(1);
  };

  // -----------------------------------------------------

  const quitExam = async (msg = "Waktu ujian Habis") => {
    try {
      const config = {
        ...endpoints.startExamUser,
        data: {
          id: dataExam.currentUser.id,
          waktu_selesai: moment().format("YYYY-MM-DD HH:mm"),
          status_ujian: "selesai",
        },
      };
      const res = await request(null, config);
      setAlertMessage(
        "Waktu Ujian Habis!\n" +
          "Ujian ini akan diselesaikan secara otomatis\n" +
          "Terima kasih telah mengikuti ujian"
      );
      setModal(true);
      setTimeout(() => {
        setModal((state) => !state);
        history.push("/login");
      }, 5000);
    } catch (error) {
      setModal(true);
      setAlertMessage(error.error.response.data.messages);
      setTimeout(() => {
        setModal((state) => !state);
        history.push("/login");
      }, 5000);
    }
  };

  // -----------------------------------------------------

  const generateNextSoal = async (ragu = false) => {
    setLoading(true);
    let res = null;
    let config = {};
    try {
      if (ragu) {
        config = {
          ...endpoints.generateSoal,
          data: {
            exam_user_id: dataExam.currentUser.id,
            level_soal_terakhir:
              dataSoal.soal[dataSoal.current.index].level_soal,
            hasil_jawaban_terakhir: null,
          },
        };
      } else {
        config = {
          ...endpoints.generateSoal,
          data: {
            exam_user_id: dataExam.currentUser.id,
            level_soal_terakhir:
              dataSoal.soal[dataSoal.current.index].level_soal,
            hasil_jawaban_terakhir:
              dataSoal.status[dataSoal.current.index].hasil,
          },
        };
      }

      res = await request(null, config);
      // console.log("res ->", res);
      if (
        res.data.exam_question_answer.tipe_jawaban == "pilihan_ganda" ||
        res.data.exam_question_answer.tipe_jawaban == "pilihan_ganda_komplex" ||
        res.data.exam_question_answer.tipe_jawaban == "benar_salah" ||
        res.data.exam_question_answer.tipe_jawaban == "survey_lingkungan"
      ) {
        const nextSoal = {
          ...res.data,
          exam_question_answer: {
            ...res.data.exam_question_answer,
            jawaban: res.data.exam_question_answer.jawaban.map((item) => {
              return {
                ...item,
                status_jawaban: "0",
              };
            }),
          },
        };
        dispatch({
          type: SET_DATA_SOAL,
          payload: nextSoal,
        });
        dispatch({
          type: SET_ID_USER_SOAL,
          payload: res.exam_user_question_id,
        });
        dispatch({
          type: SET_CURRENT_SOAL,
          payload: { index: dataSoal.current.index + 1 },
        });
      } else if (
        res.data.exam_question_answer.tipe_jawaban == "isian_singkat"
      ) {
        const nextSoal = {
          ...res.data,
          exam_question_answer: {
            ...res.data.exam_question_answer,
            jawaban: res.data.exam_question_answer.jawaban.map((item) => {
              return {
                ...item,
                text_jawaban: "",
              };
            }),
          },
        };
        dispatch({
          type: SET_DATA_SOAL,
          payload: nextSoal,
        });
        dispatch({
          type: SET_ID_USER_SOAL,
          payload: res.exam_user_question_id,
        });
        dispatch({
          type: SET_CURRENT_SOAL,
          payload: { index: dataSoal.current.index + 1 },
        });
      } else {
        dispatch({ type: SET_DATA_SOAL, payload: res.data });
        dispatch({
          type: SET_ID_USER_SOAL,
          payload: res.exam_user_question_id,
        });
        dispatch({
          type: SET_CURRENT_SOAL,
          payload: { index: 0 },
        });
      }

      setLoading(false);
    } catch (error) {
      setLoading(false);
      if (error.status === 422) {
        // quitExam(error.error.response.data.messages);
        confirmTimeEnd();
      } else {
        addToast({
          success: false,
          message:
            "Terjadi kesalahan saat mengambil soal, pastikan internet stabil",
        });
      }
    }
  };

  // -----------------------------------------------------

  const generateSoal = () => {
    // console.log("current --->", dataSoal.soal[dataSoal.current.index]);
    if (
      dataSoal.soal[dataSoal.current.index].pertanyaan_layout === "vertikal"
    ) {
      switch (
        dataSoal.soal[dataSoal.current.index].exam_question_answer.tipe_jawaban
      ) {
        case "pilihan_ganda":
          return (
            <PilihanGandaVertical
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
        case "pilihan_ganda_komplex":
          return (
            <MultipleSelectionVertical
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
        case "benar_salah":
          return (
            <BenarSalahVertikal
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
        case "isian_singkat":
          return (
            <ShortEssay dataSoal={dataSoal.soal[dataSoal.current.index]} />
          );
        case "menjodohkan":
          return (
            <MenjodohkanVertical
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
        case "survey_lingkungan":
          return (
            <SurveyLingkungan
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
        default:
          return (
            <PilihanGandaVertical
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
      }
    } else {
      switch (
        dataSoal.soal[dataSoal.current.index].exam_question_answer.tipe_jawaban
      ) {
        case "pilihan_ganda":
          return (
            <PilihanGanda dataSoal={dataSoal.soal[dataSoal.current.index]} />
          );
        case "pilihan_ganda_komplex":
          return (
            <MultipleSelection
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
        case "menjodohkan":
          return (
            <Menjodohkan dataSoal={dataSoal.soal[dataSoal.current.index]} />
          );
        case "benar_salah":
          return (
            <BenarSalahHorizontal
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
        case "isian_singkat":
          return (
            <ShortEssay dataSoal={dataSoal.soal[dataSoal.current.index]} />
          );
        case "survey_lingkungan":
          return (
            <SurveyLingkungan
              dataSoal={dataSoal.soal[dataSoal.current.index]}
            />
          );
        default:
          return (
            <PilihanGanda dataSoal={dataSoal.soal[dataSoal.current.index]} />
          );
      }
    }
  };

  // -----------------------------------------------------

  const getTimeLeft = async () => {
    try {
      const config = {
        ...endpoints.getExamTimeLeft,
        endpoint: endpoints.getExamTimeLeft.endpoint + dataExam.current.id,
      };
      const { status, data } = await request({}, config);
      if (data > 0) {
        setTimeLeft(data);
      } else {
        sendLog(data);
        quitExam();
      }
    } catch (error) {
      addToast({
        success: false,
        message:
          "Terjadi kesalahan saat mengambil waktu server, pastikan internet stabil",
      });
    }
  };

  // -----------------------------------------------------

  useEffect(() => {
    getTimeLeft();
    const interval = setInterval(() => {
      confirmTimeEnd();
    }, 120000);
    return () => clearInterval(interval);
  }, []);

  // -----------------------------------------------------

  const isLoggedIn = () => (profile.nama ? true : false);

  if (!isLoggedIn()) {
    return <Redirect to="/login" />;
  }
  // console.log("render");
  return (
    <>
      <Header>
        <CRow>
          <CCol md="12">
            <CCard className="card-rounded shadow border-white p-4 mt-2">
              <div
                className="d-flex justify-content-between pb-1"
                style={{
                  borderBottomWidth: 2,
                  borderBottomColor: "#ccc",
                  borderBottomStyle: "solid",
                }}
              >
                <div>
                  <h5 className="text-black-50 mb-0">
                    Soal Nomor{" "}
                    <strong className="text-primary">
                      {dataSoal.current.index + 1}
                    </strong>
                  </h5>
                  {/* <p className="mb-0">
                    Ukuran font soal:{" "}
                    {DATA_UKURAN_SOAL.map((item, index) => (
                      <span
                        style={{
                          fontSize: item.fontSize,
                          marginRight: 10,
                          fontWeight: item.fontWeight,
                          cursor: "pointer",
                        }}
                        key={index}
                      >
                        A
                      </span>
                    ))}
                  </p> */}
                </div>
                <div className="">
                  <CButton
                    style={{ borderRadius: 50 }}
                    color="primary"
                    className="m-1"
                    onClick={() => setShowModalInformasi(true)}
                  >
                    INFORMASI SOAL
                  </CButton>
                  <CButton
                    style={{ borderRadius: 50, fontWeight: "bold" }}
                    color="outline-danger"
                    className="m-1"
                  >
                    <span>Sisa Waktu: </span>
                    <Countdown
                      date={new Date().getTime() + timeLeft}
                      // controlled={true}
                      onComplete={() => confirmTimeEnd()}
                      // onTick={() => {}}
                      // onStart={() => console.log("start contdown")}
                    />
                  </CButton>
                  <CButton
                    style={{ borderRadius: 50 }}
                    color="primary"
                    className="m-1"
                    onClick={() => setShowModalNavigation(true)}
                  >
                    <span>Daftar Soal</span>
                    <i className="fas fa-th-large ml-2" />
                  </CButton>
                </div>
              </div>
              <div className="border-2">
                {loading ? <PageLoading /> : generateSoal()}
              </div>
              <div className="mt-2 d-flex justify-content-between">
                <CButton
                  style={{ borderRadius: 50 }}
                  color="danger"
                  // className="px-3"
                  disabled={dataSoal.current.index === 0}
                  onClick={() => {
                    window.scrollTo(0, 0);
                    dispatch({
                      type: SET_CURRENT_SOAL,
                      payload: { index: dataSoal.current.index - 1 },
                    });
                  }}
                >
                  <i className="fas fa-chevron-circle-left mr-2" />
                  <span>Soal Sebelumnya</span>
                </CButton>
                <CButton
                  style={{ borderRadius: 50 }}
                  color="warning"
                  // className="px-3"
                  disabled={
                    (dataSoal.soal.length === totalSoalUjian &&
                      dataSoal.current.index + 1 === totalSoalUjian) ||
                    loading
                  }
                  onClick={() => {
                    window.scrollTo(0, 0);
                    if (dataSoal.current.index + 2 > dataSoal.soal.length) {
                      generateNextSoal(true);
                    } else {
                      dispatch({
                        type: SET_CURRENT_SOAL,
                        payload: { index: dataSoal.current.index + 1 },
                      });
                    }
                    dispatch({
                      type: SET_STATUS_JAWAB_SOAL,
                      payload: { status: "ragu_ragu" },
                    });
                  }}
                >
                  <i className="fas fa-stop mr-2 text-white" />
                  <span className="text-white">Ragu-ragu</span>
                </CButton>
                {dataSoal.soal.length === totalSoalUjian &&
                dataSoal.current.index + 1 === totalSoalUjian ? (
                  <CButton
                    style={{ borderRadius: 50 }}
                    color="primary"
                    onClick={() => setShowModalFinishConfirmation(true)}
                  >
                    <span>Selesai</span>
                    <i className="fas fa-chevron-circle-right ml-2" />
                  </CButton>
                ) : (
                  <CButton
                    style={{ borderRadius: 50 }}
                    color="primary"
                    className="px-3"
                    disabled={loading}
                    onClick={() => {
                      if (dataSoal.current.index + 2 > dataSoal.soal.length) {
                        generateNextSoal();
                      } else {
                        dispatch({
                          type: SET_CURRENT_SOAL,
                          payload: { index: dataSoal.current.index + 1 },
                        });
                      }
                    }}
                  >
                    <span>Soal Selanjutnya</span>
                    <i className="fas fa-chevron-circle-right ml-2" />
                  </CButton>
                )}
              </div>
            </CCard>
          </CCol>
        </CRow>
      </Header>
      <CModal show={showModalInformasi} onClose={toggleShowModalInformasi}>
        <CModalHeader closeButton>
          <CModalTitle>Informasi Soal</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <div className="d-flex direction-row justify-content-start soal-item-container">
            <div
              dangerouslySetInnerHTML={{
                __html: generateHTML(
                  dataSoal.soal[dataSoal.current.index].informasi_soal
                ),
              }}
            />
          </div>
        </CModalBody>
      </CModal>
      {/* Modal Navigation*/}
      <CModal show={showModalNavigation} onClose={toggleModalNavigation}>
        <CModalHeader closeButton>
          <CModalTitle>Daftar Soal</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <div className="d-flex direction-row flex-wrap justify-content-start soal-item-container">
            {dataSoal.status.map((item, index) => {
              if (index < totalSoalUjian) {
                return (
                  <div
                    key={index}
                    style={
                      index + 1 <= dataSoal.soal.length
                        ? { cursor: "pointer" }
                        : {}
                    }
                    onClick={
                      index + 1 <= dataSoal.soal.length
                        ? () => {
                            dispatch({
                              type: SET_CURRENT_SOAL,
                              payload: { index },
                            });
                          }
                        : () => {}
                    }
                    className={
                      "text-white soal-item " +
                      (index === dataSoal.current.index
                        ? "bg-primary"
                        : item.status === "dijawab"
                        ? "bg-success"
                        : item.status === "ragu_ragu"
                        ? "bg-warning"
                        : "bg-secondary")
                    }
                  >
                    {index + 1}
                  </div>
                );
              }
              return null;
            })}
          </div>
        </CModalBody>
      </CModal>
      {/* Modal Finish */}
      <CModal
        show={showModalFinishConfirmation}
        onClose={toggleModalFinishConfirmation}
      >
        <CModalHeader closeButton>
          <CModalTitle>Konfirmasi Tes</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <p className="mb-0">
            Anda masih memiliki waktu lebih dari 60 detik untuk mata uji ini
          </p>
          <p className="mb-0">
            Apakah anda yakin ingin mengakhiri mata uji ini?
          </p>
          <CFormGroup style={{ marginBottom: 3 }}>
            <CInput
              type="checkbox"
              label="Centang kemudian tekan tombol selesai"
              checked={confirmExit}
              onChange={() => setConfirmExit(!confirmExit)}
            />
          </CFormGroup>
        </CModalBody>
        <CModalFooter>
          <div
            style={{
              width: "100%",
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
            }}
          >
            <Link to={"/exam-finish"}>
              <CButton
                color="danger"
                shape="pill"
                size="lg"
                disabled={!confirmExit}
              >
                SELESAI
              </CButton>
            </Link>
            <CButton
              color="success"
              shape="pill"
              onClick={() => setShowModalFinishConfirmation(false)}
              className="mx-2 px-4"
              size="lg"
            >
              TIDAK
            </CButton>
          </div>
        </CModalFooter>
      </CModal>
      <CModal show={modal} onClose={toggle} closeOnBackdrop={false}>
        <CModalHeader closeButton>Informasi</CModalHeader>
        <CModalBody>{alertMessage}</CModalBody>
        <CModalFooter>
          <CButton color="primary" onClick={toggle}>
            OK
          </CButton>
        </CModalFooter>
      </CModal>
      {/* Toaster */}
      {Object.keys(toasters).map((toasterKey) => (
        <CToaster position={toasterKey} key={"toaster" + toasterKey}>
          {toasters[toasterKey].map((toast, key) => {
            return (
              <CToast
                key={"toast" + key}
                show={true}
                autohide={toast.autohide}
                fade={toast.fade}
              >
                <CToastHeader
                  closeButton={toast.closeButton}
                  className={`${
                    toast.success ? "bg-primary" : "bg-danger"
                  } text-white`}
                >
                  Informasi
                </CToastHeader>
                <CToastBody>{toast.message}</CToastBody>
              </CToast>
            );
          })}
        </CToaster>
      ))}
    </>
  );
}

export default Exam;
